import React from 'react';
import './App.css';
import VerticalTabs from "./components/VerticalTabs";
import {createMuiTheme, responsiveFontSizes, ThemeProvider} from "@material-ui/core/styles";

let theme = createMuiTheme({
    typography: {
        fontFamily: "Garamond",
        h4: {
            fontWeight: "bold",
            fontSize: "2.1vw"
        },
        h6: {
            fontWeight: "bold",
        },
        button: {
            fontSize: "1.05vw",
            fontWeight: "bold"
        },
        body1: {
            //fontSize: "0.90vw"
            fontSize: "1.02vh"
        }
     },
    overrides: {
        MuiButton: {
            root: {
                "&$disabled": {
                    color: "black"
                }
            }
        }
    }

});
theme = responsiveFontSizes(theme);

function App() {
    return (
        <ThemeProvider theme={theme}>
            <VerticalTabs/>
        </ThemeProvider>
    );
}

export default App;
